import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import {
  ShowModalParameterRugiNota,
  showModal,
  getParameterRugiNota,
  hideModal,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import {  ToastNotification } from "../../../components/helpers/notification.jsx";
import FormParameterRugiNota from "./FormParameterRugiNota.jsx";
import { reset } from "redux-form";
import {  postData, putData } from "../../../config/axios.jsx";
import { ErrorHandling } from "../../../components/helpers/function.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

const maptostate = (state) => {
  return {
    dataRugiNota : state.datamaster.getParameterRugiNota,
    hideModal: state.datamaster.modalDialog,
  };
};

class MasterParameterRugiNota extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      isLoading: false,
      modalDialog: false,
      columns: [
        {
          dataField: "kode_group",
          text: "Kode Kategori",
          sort: true,
        },
        {
          dataField: "kadar",
          text: "kadar",
        },
        {
          dataField: "rugi",
          text: "Rugi",
        },
        {
          dataField: "rusak",
          text: "Rusak",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              kode_group: row.kode_group,
              kadar: row.kadar,
              rugi: row.rugi,
              rusak: row.rusak,
            };
             
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEditParameterRugiNota(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                     <i className="fa fa-edit"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
      
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    this.props.dispatch(getParameterRugiNota())
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    let data_post = {
      kode_group: data.kode_group,
      kadar : data.kadar,
      rugi : data.rugi,
      rusak : data.rusak
    }
    let data_edit = {
      kadar : data.kadar,
      rugi : data.rugi,
      rusak : data.rusak
    }
    this.state.isEdit
    ? putData("ruginota/edit/by-kode-group/" + data.kode_group, data_edit)
        .then(() => {
          this.props.dispatch(hideModal());
        })
        .then(() => {
          ToastNotification("success", "Data Berhasil Diedit");
        })
        .then(() => {
          this.props.dispatch(getParameterRugiNota());
        })
        .then(() => {
          this.setState({ isLoading: false });
        })
        .then(() => {
          this.props.dispatch(reset("ModalParameterRugiNota"));
        })
        .catch((error) => {
          ToastNotification('info', ' Data Gagal Diedit')
        })
    :
    postData("ruginota/add", data_post)
      .then(() => {
        this.props.dispatch(hideModal());
      })
      .then(() => {
        ToastNotification("success", "Data Berhasil Simpan");
      })
      .then(() => {
        this.props.dispatch(getParameterRugiNota());
      })
      .then(() => {
        this.setState({ isLoading: false });
      })
      .then(() => {
        this.props.dispatch(reset("ModalParameterRugiNota"));
      })
      .catch((error) => {
        ErrorHandling(
          error,
          "jenis/reactive/by-kode-jenis/",
          data.kode_group,
          data_post,
          this.props.dispatch,
          getParameterRugiNota(),
          hideModal(),
          "ModalParameterRugiNota"
        ).then(() => {
          this.setState({ isLoading: false });
        });
      });
  }
  formEditParameterRugiNota(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalParameterRugiNota(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  ShowModalParameterRugiNota() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalParameterRugiNota(false));
    this.setState({
      isEdit: false,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Master Parameter Rugi Nota</li>
        </ol>
        <h1 className="page-header">Master Parameter Rugi Nota </h1>
        <Panel>
          <PanelHeader>Master Parameter Rugi Nota</PanelHeader>
          <br />

          {/* Master Parameter Rugi Nota */}
          <div className="container">
            <ToolkitProvider
              keyField="kode_group"
              data={this.props.dataRugiNota || []}
              columns={this.state.columns}
              search
              exportCSV={{
                fileName: "Export Master Parameter Rugi Nota.csv",
              }}
            >
              {(props) => (
                <div className="row">
                  <div className="col-6">
                    <button
                      onClick={() => this.ShowModalParameterRugiNota()}
                      className="btn btn-primary"
                    >
                      {" "}
                      <i className="fa fa-plus"></i>{" "}
                    </button>
                  </div>
                  <div className="col-6">
                    <div className="text-right">
                      <SearchBar {...props.searchProps} />
                    </div>
                  </div>
                  <hr />
                  <div className="col-12">
                    <BootstrapTable
                      pagination={paginationFactory()}
                      {...props.baseProps}
                    />
                    <br />
                    <ExportCSVButton {...props.csvProps}>
                      Export CSV!!
                    </ExportCSVButton>
                  </div>
                </div>
              )}
            </ToolkitProvider>
          </div>
          <br />
          {/* End Master Pinjaman */}
          <ModalGlobal
             title={
              this.state.isEdit
                ? "Edit Data Parameter Rugi Nota"
                : "Tambah Data Parameter Rugi Nota"
            }
            size="P"
            content={
              <FormParameterRugiNota
              isEdit={this.state.isEdit}
              isLoading={this.state.isLoading}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          }
          />

          {/* End Tambah Master Parameter Rugi Nota  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(MasterParameterRugiNota);
