import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { GetDataGroup, hideModal } from "../../../actions/datamaster_action";
import { ReanderField, ReanderSelect } from "../../../components/helpers/field";
import ValidasiParameterRugiNota from "../../../Validasi/ValidasiParameterRugiNota.jsx";
import Loading from 'react-fullscreen-loading';

const maptostate = (state) => {
  if (state.datamaster.ShowModalParameterRugiNota !== null) {
    return {
      datagroup: state.datamaster.GetDataGroup,
      initialValues: {
        kode_group: state.datamaster.ShowModalParameterRugiNota.kode_group,
        kadar: state.datamaster.ShowModalParameterRugiNota.kadar,
        rugi: state.datamaster.ShowModalParameterRugiNota.rugi,
        rusak: state.datamaster.ShowModalParameterRugiNota.rusak,
      },
    };
  }
};

class FormParameterRugiNota extends Component {
  state = {
    aktif : false
  };
  componentDidMount() {
    this.props.dispatch(GetDataGroup());
    // setTimeout(() => {
    //   document.getElementById('kode_group').focus()
    // }, 100);
    setTimeout(() => {
      this.props.isEdit
        ? document.getElementById("kadar").focus()
        : document.getElementById("kode_group").focus();
    }, 100);
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        autoComplete="off"
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
      >
        <div className="form-group">
          <Field
            id="kode_group"
            label="Kode Kategori"
            name="kode_group"
            options={this.props.datagroup
              .filter((list) => list.kode_group !== undefined)
              .map((list) => {
                let data = {
                  value: list.kode_group,
                  name: list.kode_group + " - " + list.nama_group,
                };
                return data;
              })}
            value={this.props.kode_group}
            disabled={this.props.isEdit}
            placeholder={"Silahkan Pilih Kode Kategori"}
            component={ReanderSelect}
          />
        </div>
        <Field
          id="kadar"
          name="kadar"
          component={ReanderField}
          type="number"
          label="Kadar"
          // normalize={NumberOnly}
          placeholder="Masukan Kadar"
        />
        <Field
          name="rugi"
          component={ReanderField}
          type="number"
          label="Rugi"
          // normalize={NumberOnly}
          placeholder="Masukan Rugi"
        />
        <Field
          name="rusak"
          component={ReanderField}
          type="number"
          // normalize={NumberOnly}
          label="Rusak"
          onBlur={() => this.setState({ aktif: true })}
          placeholder="Masukan Rusak"
        />
        <div className="row">
          <div className="col-12 text-right">
          <button
              className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
            &nbsp;
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
            >
              Batal
            </button>
            &nbsp;
            
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

FormParameterRugiNota = reduxForm({
  form: "ModalParameterRugiNota",
  enableReinitialize: true,
  validate: ValidasiParameterRugiNota,
})(FormParameterRugiNota);
export default connect(maptostate, null)(FormParameterRugiNota);
